@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.my-nav {
    background-color: black;
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    /* box-shadow: 50px 20px 10px grey; */
}

.navlinks {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.navlinks > div {
    padding: 20px;
}

.navlink > a {
    color: white;
    text-decoration: none;
}


.home {
    width: 80%;
    text-align: center;
}

.title {
    padding-bottom: 20px;
}

.total>h2, .active>h2, .recovered>h2, .deaths>h2 {
    font-weight: bold;
}

.total {
    color: rgb(41, 41, 238);
}

.active {
    color: rgb(255, 136, 0);
}

.recovered {
    color: green;
}

.deaths {
    color: red;
}

.statesCases {
    text-transform: capitalize;
    width: 100%;
}

.state-t {
    width: 30%;
}

total-t, active-t, recovered-t, deaths-t {
    width: 15%;
}

th {
    background-color: rgb(226, 228, 227);
    padding: 5px;
    border: 1px solid #000000;
}

.data>.state {
    text-align: left;
    font-weight: bold;
}

.data>td {
    border: 1px solid #000000;
    padding: 5px;
}

@media screen and (max-width: 768px) {
    h1 {
        font-size: 32px;
    }
    .total-data h2, .total-data span {
        font-size: 20px;
    }
    th, td {
        font-size: 14px;
    }
}
*, html, body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    margin-left: auto;
    margin-right: auto;
}

html, body {
    width: 100vw;
    overflow-x: hidden;
}

.footer {
    width: 100vw;
    background-color: black;
    color: white;
    padding: 30px;
    text-align: center;
    margin-top: 10px;
}


.footer-text > a {
    text-decoration: none;
    color: yellow;
}
